<template>
    <div class="backdrop" @click.self="closeModal">
        <div class="modal" :class="{sale: theme === 'sale'}">
            <slot> You can write default content here. So as default, its gonna show this text </slot>
            <div class="actions">
                <slot name="links"></slot>
            </div>
        </div>
    </div>
</template>


<script>
export default {
    props: {
        theme: String
    },
    methods: {
        closeModal(){
            this.$emit('close')
        }
    }
}
</script>


<style>
    .modal{
        width: 400px;
        padding: 20px;
        margin: 100px auto;
        background-color: white;
        border-radius: 10px;
    }
    .backdrop{
        position: fixed;
        top: 0;
        background-color: rgba(0, 0, 0, 0.5);
        width: 100%;
        height: 100%;
    }
    .modal h1{
        color: rgb(143, 214, 158);
        padding: 0;
        border: none;
    }
    .modal p{
        font-style: normal;
    }

    .modal .actions{
        text-align: center;
        margin: 30px 0 10px 0;
    }

    .modal .actions a{
        color: #333;
        padding: 8px;
        border: 1px solid #eee;
        border-radius: 5px;
        margin: 10px;
        text-decoration: none;
    }

    .modal.sale{
        background-color: crimson;
        color: white;
    }
    .modal.sale h1{
        color: white;
    }

    .modal.sale .actions a{
        color: white;
    }
</style>